.resources-center .center-content .center-content-center .paper-center-div {
  width: 221px;
  height: 110px;
  background: #FAFAFA;
  border: 1px solid #EDEDED;
  box-sizing: border-box;
  border-radius: 3px;
  margin: 0 20px 20px 0;
}
.resources-center .center-content .center-content-center .paper-center-div p {
  margin: 0 0 6px 36px;
}
.resources-center .center-content .center-content-center .paper-center-div p span {
  font-size: 12px;
  color: #000000;
  opacity: 0.3;
}
.resources-center .center-content .center-content-center .paper-center-div p:first-child {
  margin: 14px 0 6px 14px;
}
.resources-center .center-content .center-content-center .paper-center-div p:first-child span {
  font-size: 14px;
  color: #000000;
  margin-left: 10px;
  opacity: 01;
}
.resources-center .paper-right-bottom {
  overflow: hidden;
  /*box-shadow: 2px 8px 16px -8px rgba(29, 32, 31, 0.12);*/
  border-radius: 6px;
  background: #fff;
  min-height: 300px;
}
.resources-center .paper-right-bottom .question-select {
  padding: 0 15px;
  text-align: center;
}
.resources-center .paper-right-bottom .ant-card-head {
  border-bottom: none;
}
.resources-center .rside-col {
  margin-bottom: 15px;
  width: 20%;
}
.resources-center .rside-col dl {
  display: flex;
  background: #FAFAFA;
  border: 1px solid #EDEDED;
  box-sizing: border-box;
  border-radius: 6px;
  overflow: hidden;
  min-width: 212px;
}
.resources-center .rside-col dt .img {
  width: 47px;
  height: 83px;
  position: relative;
}
.resources-center .rside-col dt .img .img01 {
  width: 47px;
  height: 83px;
}
.resources-center .rside-col dt .img .img02 {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: block;
  height: 15px;
  width: auto;
}
.resources-center .rside-col dd {
  padding: 12px 10px 0;
  box-sizing: border-box;
  width: 100%;
  overflow: hidden;
}
.resources-center .rside-col dd div {
  color: #000;
  padding-left: 5px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  height: 21px;
  font-style: normal;
  font-weight: normal;
  cursor: pointer;
}
.resources-center .rside-col dd p {
  margin: 0;
}
.resources-center .rside-col dd p span {
  margin: 0 5px;
  color: rgba(0, 0, 0, 0.3);
  font-size: 12px;
}
.resources-center .rside-col dl:hover div {
  color: #3BB9FF;
}
.resources-center .my-collect-tree {
  display: block;
  overflow-y: auto;
  padding: 0 10px;
}
.resources-center .my-collect-tree .anticon svg {
  border: 1px solid #3BB9FF;
  color: #DEF3FF;
}
.resources-center .my-collect-tree .anticon svg path:first-child {
  color: #3BB9FF;
}
.resources-center .my-collect-tree .TreeCircleIcon {
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background: #3BB9FF;
  display: inline-block;
  margin-bottom: 4px;
}
.resources-center .my-collect-tree::-webkit-scrollbar {
  /*滚动条整体样式*/
  width: 6px;
  height: 6px;
  background-color: #ffffff;
}
.resources-center .my-collect-tree::-webkit-scrollbar-thumb {
  /*滚动条里面小方块*/
  -webkit-box-shadow: inset 0 0 6px #DDDDDD;
  background-color: #DDDDDD;
}
.resources-center .my-collect-tree::-webkit-scrollbar-track {
  /*滚动条里面轨道*/
  -webkit-box-shadow: inset 0 0 6px #ffff;
  border-radius: 40px;
}
html {
  overflow-x: auto;
  overflow-y: auto;
}
